.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #0A1A28;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
  width: 80%;
  max-width: 600px;
  border: 2px solid #79F2D7;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 100;
  backdrop-filter: blur(5px);
}

.modal-content {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.subtask-add-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  width: 90%;
}

.subtask-add-input-field {
  flex-grow: 1;
  padding: 0.5rem;
  border-radius: 10px;
  font-size: 1rem;
  border: 2px solid #79F2D7;
  color: #79F2D7;
  background-color: transparent;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.2s ease-in-out;
  margin-right: 1rem;
}

.subtask-add-input-field:focus {
  outline: none;
    /*border: 2px solid #79F2D7;*/
  font-size: 1rem;
  font-weight: bold;
}

.subtask-add-input-field::placeholder {
  color: #9b9b9b;
}




